import { Component } from '@angular/core';
import { ToastController } from '@ionic/angular';
import { AlertController } from '@ionic/angular';
import { GroceriesServiceService } from '../groceries-service.service';
import { InputDialogService } from '../input-dialog.service';
import { SocialSharing } from '@ionic-native/social-sharing/ngx';

@Component({
  selector: 'app-tab1',
  templateUrl: 'tab1.page.html',
  styleUrls: ['tab1.page.scss']
})
export class Tab1Page {
  title = 'Groceries';
  items = [];

  constructor(public toastController: ToastController, public alertController: AlertController, public dataService: GroceriesServiceService, public inputDialogService: InputDialogService, public socialSharing: SocialSharing) {
  }

  loadItems() {
    return this.dataService.getItems();
  }

  async removeItem(item, index) {
    console.log("Removing Item - ", item, index)
    const toast = await this.toastController.create({
      message: 'Removing ' + item.name + ' from grocery list...',
      duration: 2000
    });
    toast.present();

    this.dataService.removeItem(index);
  }

  async shareItem(item, index) {
    console.log("Sharing Item - ", item, index)
    const toast = await this.toastController.create({
      message: 'Sharing ' + item.name + ' from grocery list...',
      duration: 2000
    });
    toast.present();

    let message = "Grocery Item: " + item.name + "Quantity: " + item.quantity;
    let subject = "Shared via Groceries App";

    this.socialSharing.share(message, subject).then(() => {
      console.log("Shared successfully.")
    }).catch((error) => {
      console.error("Error while sharing ", error);
    });
  }

  async editItem(item, index) {
    console.log("Edit Item - ", item, index)
    const toast = await this.toastController.create({
      message: 'Editing ' + item.name + ' from grocery list...',
      duration: 2000
    });
    toast.present();
    this.inputDialogService.presentPrompt(item, index);
  }

  addItem() {
    console.log("Item added.");
    this.inputDialogService.presentPrompt();
  }
}